* {
    margin: 0;
    box-sizing: border-box;
}
body {
    background: 
    transparent 
    url('/img/coffee-photo.jpg')
    center /
    cover
    no-repeat
    scroll;
    height: 100vh;
}
.container {
    background-color:#6F4E37;
    opacity: 0.9;
    z-index: 1;
    margin-right: 0;
    margin-left: auto;
    width: 35vw;
    height: 100vh;

}
.info {
    color: white;
    width: 35vw;
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 3em;
    z-index: 2;

    h1 {
        font-size: 220%;
        text-transform: uppercase;
    }
    p {
        line-height: 140%;
        margin-top: 10vh;
        margin-bottom: 10vh;
    }
}
button {
    padding: 0.5em;
    background-color: red;
    color: white;
    border: .5px solid white;
    font-size: 170%;
    border-radius: 5px;
}